:root {
    --background-light: #ffffff;
    --background-dark: #1a1a1a;
    --text-light: #000000;
    --text-dark: #ffffff;
}

body {
    background-color: var(--background-light);
    color: var(--text-light);
    transition: background-color 0.3s ease, color 0.3s ease;
}

body.dark-mode {
    background-color: var(--background-dark);
    color: var(--text-dark);
}
